
 .demo {
    background-color: var(--background);
    display: flex;
    justify-content: center;

 }
 .kanban-wrapper {
    border-radius: 1rem;
    box-shadow: 0 7px 29px 0 #64646f33;
    // overflow: hidden;
    width: min-content;
     margin-bottom: 2rem;
    // background-color: var(--background);
    overflow: auto;

}
.kanban-container {
    grid-gap: 1rem;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3,minmax(-webkit-min-content,40px));
    grid-template-columns: repeat(3, minmax(min-content, 40px));
    justify-content: flex-start;
    padding: 1rem;
    overflow-x: scroll;
    transition: all .3s ease;

  .kanban {
    background-color: var(--white);
    border-radius: 1rem;

    .main-title {
      padding: 0 1rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: var(--text-color);
      border-top-left-radius: 1rem;
      border-top-right-radius: 1rem;
      h2 {
        font-size: 24px;
      }
      .close-btn {
        cursor: pointer;
      }
      .status-icon {
        cursor: pointer;
      }
    }
    .main-title.kanban-1 {
      background-color: var(--kanban-open-bg)
    }
    .main-title.kanban-2 {
      background-color: var(--kanban-inprogress-bg)
    }
    .main-title.kanban-3 {
      background-color: var(--kanban-closed-bg)
    }

    .title {
      padding: 0 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--text-color);
      h2 {
        font-size: 2.3rem;
        color: var(--text-color);
      }
      .close-btn {
        cursor: pointer;
      }
    }

    .columns-container {
      display: flex;
      justify-content: space-between;
      padding: 0 1rem;
      flex-wrap: wrap;
    }
    .kanban-column {
      padding: 1rem;
      display: flex;
      background-color: transparent;
      flex-direction: column;
      width: 100%;
      min-height: 70px;
      min-width: 300px;
      max-width: max-content;
      border-bottom-left-radius: 1rem;
      border-bottom-right-radius: 1rem;
      h3 {
        color: var(--text-color);
      }

      .kanban-tasks-list {
        min-height: 100px;
        border-radius: 4px;
        padding-top: 0.5rem;

        &.dragging-over {
          background-color: var(--background); // Change the background color when dragging over
        }
        &.can-drop {
          border: 2px dashed #1e88e5; // Change the border when a task can be dropped
        }
      }
      &.can-drop {
        border: 2px dashed #1e88e5; // Change the border when a task can be dropped
      }
    }

    .kanban-task {
      background-color: var(--kanban-task-bg);
      border: 1px solid #ddd;
      border-radius: 4px;
      padding: 8px;
      margin-bottom: 8px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      max-width: 300px;
      &.can-drop {
        border: 2px dashed #1e88e5; // Change the border when a task can be dropped
      }
      &:hover {
        background-color: var( --kanban-task-bg-hover); // Change the background color on hover
      }
      .task-title-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 12px;
        gap: 1rem;
      }
      .task-content {
        display: flex;
        flex-direction: column;
        gap: 0.4rem;
        padding: 1.5rem 0;
        h3 {
          color: var(--blue);
          line-height: 1.3;
          font-size: 16px;
        }
        p {
          text-align: start;
          font-size: 14px;
        }
      }
    }
  }
}

.kanban-container .kanban {
  transition: max-height 0.5s ease, width 0.5s ease;
  // overflow: hidden;

  .main-title {
    transition: padding 0.5s ease, flex-direction 0.5s ease;

    h2 {
      transition: transform 0.5s ease;
    }
  }

  .columns-container {
    transition: opacity 0.5s ease, transform 0.5s ease;
    opacity: 1;
    transform: scaleY(1);
    transform-origin: top;
  }

  .kanban-column {
    transition: opacity 0.5s ease;

    h3,
    .kanban-tasks-list {
      transition: opacity 0.5s ease;
      opacity: 1;
    }
  }

  &.minimize {
    width: 40px;
    max-height: 200px;

    .main-title {
      padding: 1.3rem 0 3.3rem 0;
      flex-direction: column-reverse;
    height: 100%;

      h2 {
        transform: rotate(-90deg);
      }
    }

    .columns-container {
      opacity: 0;
      transform: scaleY(0);
      pointer-events: none;
    }

    .kanban-column {
      h3,
      .kanban-tasks-list {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}


.dragging-over {
  background-color: #e0f7fa;
}
.dragging {
  opacity: 0.7;
  background-color: #bbdefb;
}
.hidden {
  display: none;
}
.kanban-task.dragging {
  opacity: 0.8;
  background-color: var(--kanban-task-drag-bg);
  border: 2px solid var(--kanban-task-drag-border);
  transform: rotate(2deg);
  transition: all 0.2s ease;
}

/* אזור המשימות בזמן גרירה */
.kanban-tasks-list.highlight-drop {
  border: 2px dashed var(--kanban-dropzone-border);
  border-radius: 6px;
  min-height: 80px;
  background-color: var(--kanban-dropzone-bg);
}

.highlight-drop {
  border: 2px dashed var(--kanban-dropzone-border);
  background-color: var(--kanban-dropzone-highlight-bg);
}
.kanban-group {
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
}


@media screen and (max-width: 768px) {
  .kanban-container .kanban:not(:first-child) {
    display: none;
  }
  .kanban-container {
    grid-gap:0;
    gap: 0;
}
}
.kanban-task.dragging {
  position: fixed !important;
  // bottom: 0;
  right: 0;
  z-index: 9999;
  pointer-events: none;
  // transform: rotate(2deg);
    z-index: 9999 !important;
  width: 300px;
  max-width: 90vw;
  pointer-events: none;
  transform: rotate(2deg);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.25);
}

